import { Link } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';
import { Grid } from 'react-loader-spinner';
import { useQuery } from 'react-query';
import { fetchCoins } from '../API/Api';
import { Helmet } from 'react-helmet';
import { useSetRecoilState } from 'recoil';
import { isDarkAtom } from '../../atom';

const Loader = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
`;

const spinAnimation = keyframes`
    to {
        transform: rotate(360deg);
    }
`;

const StyledGrid = styled(Grid)`
    border: 8px solid #ff0000;
    border-top: 8px solid transparent;
    border-radius: 50%;
    animation: ${spinAnimation} 1s linear infinite;
`;

const Container = styled.div`
    padding: 0px 20px;
    margin: 0 auto;
    max-width: 480px;
`;

const Header = styled.header`
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const CoinsList = styled.ul``;

const Coin = styled.li`
    background-color: ${(props) => props.theme.cardBgColor};
    color: ${(props) => props.theme.textColor};
    border-radius: 15px;
    margin-bottom: 10px;
    border: 1px solid white;
    a {
        display: flex;
        align-items: center;
        padding: 20px;
        transition: color 0.2s ease-in;
    }
    &:hover {
        a {
            color: ${(props) => props.theme.accentColor};
        }
    }
`;

const Title = styled.h1`
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 45px;
    color: ${(props) => props.theme.accentColor};
`;

const Img = styled.img`
    width: 3.5rem;
    height: 3.5rem;
    margin-right: 15px;
`;

const ToggleButton = styled.button`
    background-color: ${(props) => props.theme.toggleButtonBgColor};
    color: ${(props) => props.theme.toggleButtonTextColor};
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;

    &:hover {
        background-color: ${(props) => props.theme.toggleButtonBgColorHover};
        color: ${(props) => props.theme.toggleButtonTextColorHover};
    }
`;

interface ICoin {
    id: string;
    name: string;
    symbol: string;
    rank: number;
    is_new: boolean;
    is_active: boolean;
    type: string;
}

function Coins() {
    const setDarkAtom = useSetRecoilState(isDarkAtom);
    const toggleDarkAtom = () => setDarkAtom((prev) => !prev);
    const { isLoading, data } = useQuery<ICoin[]>(['allcoins'], fetchCoins);
    return (
        <Container>
            <Helmet>
                <title></title>
            </Helmet>
            <Header>
                <Title>Cryptocurrencies</Title>
                <ToggleButton onClick={toggleDarkAtom}>Toggle Mode</ToggleButton>
            </Header>
            {isLoading ? (
                <Loader>
                    <StyledGrid color="#ff0000" height={250} width={250} />
                </Loader>
            ) : (
                <CoinsList>
                    {data?.slice(0, 100).map((coin) => (
                        <Coin key={coin.id}>
                            <Link to={`/${coin.id}`} state={coin}>
                                <Img src={`https://cryptocurrencyliveprices.com/img/${coin.id}.png`} />
                                {coin.name} &rarr;
                            </Link>
                        </Coin>
                    ))}
                </CoinsList>
            )}
        </Container>
    );
}

export default Coins;
