import { useQuery } from 'react-query';
import styled from 'styled-components';
import { fetchHistory } from '../API/Api';
import { useOutletContext } from 'react-router-dom';

const Container = styled.div`
    display: grid;
    grid-template:
        'a a'
        'a a';
`;

const List = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid ${(props) => props.theme.textColor};
    border-radius: 20% / 50%;
    padding: 10px;
    margin: 20px;
    & span:nth-child(2) {
        margin-top: 10px;
        font-weight: bold;
    }
`;

interface ICoinID {
    coinId: string;
}

interface IHistorical {
    time_open: number;
    time_close: number;
    open: string;
    high: string;
    low: string;
    close: string;
    volume: string;
    market_cap: number;
}

function Price() {
    const { coinId } = useOutletContext<ICoinID>();
    const { isLoading, data } = useQuery<IHistorical[]>(['ohlcv', coinId], () => fetchHistory(coinId), {
        refetchInterval: 10000,
    });
    const todayObjData: any = data ? data[0] : {};
    const displayKey = ['open', 'close', 'low', 'high'];
    const displayEmoji = ['', '', '', ''];
    const priceData = displayKey.map((data) => todayObjData[data]);

    return (
        <div>
            {isLoading ? (
                'Loading chart...'
            ) : (
                <>
                    <Container>
                        {displayKey.map((data, index) => {
                            const currentPrice = priceData[index];
                            return (
                                <List key={index}>
                                    <span>
                                        {displayEmoji[index]}
                                        {data}
                                    </span>
                                    <span>
                                        ${currentPrice !== undefined ? parseFloat(currentPrice).toFixed(2) : 'N/A'}
                                    </span>
                                </List>
                            );
                        })}
                    </Container>
                </>
            )}
        </div>
    );
}

export default Price;
